	module.exports = {"data":{"markdownRemark":{"html":"<h2>First blog post</h2>\n<p>I just installed Pelican for my professional blog. My intention is to blog once every 1-2 weeks. Let's see....</p>\n<p>Why did I chose Pelican?</p>\n<ul>\n<li>I wanted something simple </li>\n<li>A wanted to host it at Github</li>\n<li>The source of the blog is kept in a Github repository</li>\n<li>Some posts will consist of a Jupyter notebook</li>\n<li>A docker image should exist to build the static site</li>\n</ul>\n<p>These were the resources I used to setup Pelican:</p>\n<ul>\n<li><a href=\"http://pmbaumgartner.github.io/some-tips-for-using-jupyter-notebooks-with-pelican.html\">http://pmbaumgartner.github.io/some-tips-for-using-jupyter-notebooks-with-pelican.html</a></li>\n<li><a href=\"http://github.com/dwjbosman/docker-pelican.git\">http://github.com/dwjbosman/docker-pelican.git</a></li>\n</ul>\n<p>Before starting with Pelican, I also checked Jekkyl. Jekkyl is another static site generation tool. It's main advantage is that Github Pages natively supports this tool. So there's no need to push the output of the static site build tool to a repo. You can just update the blog source repo and Github will update your site. Unfortunately Github will only allow a very limited set of Jekkyl plugins.</p>","timeToRead":1,"excerpt":"First blog post I just installed Pelican for my professional blog. My intention is to blog once every 1-2 weeks. Let's see.... Why did I…","frontmatter":{"title":"First Post","cover":"/logos/logo-1024.png","date":"2017-11-20 20:00","category":"blaat","tags":["github","blog"]},"fields":{"nextTitle":"LSTM neural network for sequence learning","nextSlug":"/lstm-neural-network-for-sequence-learning","prevTitle":"Basic Microblaze with bootloader setup","prevSlug":"/basic-microblaze-with-bootloader-setup","slug":"/first-post"}}},"pathContext":{"slug":"/first-post"}}

